@import '../../../../../styles/templarios.abstracts';

:host {
  display: block;
}

::slotted([slot='text']) {
  display: block;
  margin-bottom: 16px;
}

::slotted([slot='links']) {
  display: flex;
  align-items: center;
  // TODO confirmar o uso de gap para flex!!!!
  gap: 24px;
  flex-wrap: wrap;

  a {
    height: 48px;
    width: 48px;
    border-radius: 50%;
    cursor: pointer;
    overflow: hidden;
  }
}
